import {useState} from "react";


export default {
    title: 'React Memo',

}

export const ReactMemo = () => {

    const [count, setCount] = useState(0)

    const [user, setUser] = useState(['Dimych', 'Valera', 'Julian'])

    return (
        <>
            <Header count={count}/>
            <Table user={user} />
        </>
    )
};

type HeaderType = {
    count: number
}

export const Header = (props: HeaderType) => {
    <button></button>
    return <div>
        {props.count}
    </div>
};

type TableType = {
    user:Array<string>
}
export const Table = (props:TableType) => {
    return <div>
        {props.user.map((el)=>{
            return(
                <li>
                    {el}
                </li>
            )
        })}
    </div>
};


